import { generateFirestoreTimestamp } from '../lib/firebase';
import { League, LeagueInput } from '../types/League';
import { User } from '../types/User';

type LeagueIdFields = Pick<League, 'leagueid' | 'platform'>;

export function generateDocumentLeagueId(league: LeagueIdFields) {
    return `${league.platform}-${league.leagueid}`;
}

export function generateMergedLeagueId(league1: LeagueIdFields, league2: LeagueIdFields) {
    return `${league1.platform}-${league1.leagueid}--${league2.leagueid}-${league2.platform}`;
}

export function completeLeague(user: User, league: LeagueInput): League {
    return {
        createdAt: generateFirestoreTimestamp(new Date()),
        buildWeek: '',
        buildYear: '',
        id: generateDocumentLeagueId(league),
        userId: user.uid,
        ...league,
    };
}
