import React from 'react';
import { useLeagueStore } from '../../store/league';
import { useProtectedRoute } from '../../hooks/useProtectedRoute';
import BaseWrapper from '../BaseWrapper/BaseWrapper';
import Button from '../theme/Button';

function LeagueColumn({ league, onClick: handleClick, selectedPlayers }) {
    return (
        <div className="merge-leagues__column">
            <h2>League {league.name}</h2>
            <ul>
                {league.users.map((player) => {
                    const includesCurrentPlayer = selectedPlayers.find((p) => p.id === player.id);
                    return (
                        <li key={player.id}>
                            <button
                                className={
                                    includesCurrentPlayer ? 'merge-leagues__selected-user' : ''
                                }
                                onClick={() => handleClick(player)}
                            >
                                {player.name}
                            </button>
                        </li>
                    );
                })}
            </ul>
        </div>
    );
}

const MergeLeagues = ({ ...params }) => {
    const { leagueid } = params;
    const { leagues, updateLeague } = useLeagueStore((state) => {
        return {
            leagues: state.leagues,
            updateLeague: state.updateLeague,
        };
    });
    const { loaded } = useProtectedRoute();

    const [selectedPlayers, setSelectedPlayers] = React.useState([]);
    const [primaryPlayer, setPrimaryPlayer] = React.useState(null);
    const [finalUsers, setFinalUsers] = React.useState([]);
    const [editingUser, setEditingUser] = React.useState(null);
    function getPlayersId(players = selectedPlayers) {
        const newPlayers = players.filter((player) => player?.id !== primaryPlayer?.id);
        if (primaryPlayer) {
            newPlayers.unshift(primaryPlayer);
        }

        return newPlayers.map((player) => player.name).join('--');
    }
    const isEditMode = !!editingUser;

    const selectedPlayersId = getPlayersId(selectedPlayers);

    React.useEffect(() => {
        if (selectedPlayers.length === 0) {
            setPrimaryPlayer(null);
        } else if (!selectedPlayers.includes(primaryPlayer)) {
            setPrimaryPlayer(selectedPlayers[0]);
        }
    }, [selectedPlayers]);

    React.useEffect(() => {
        if (!editingUser) {
            setSelectedPlayers([]);
        }
    }, [editingUser]);

    if (!loaded || leagues.length === 0) return null;

    const mergedLeague = leagues.find((league) => {
        return league.id === leagueid;
    });

    const league1 = leagues.find((league) => {
        return mergedLeague.leagues[0] === league.id;
    });

    const league2 = leagues.find((league) => {
        return mergedLeague.leagues[1] === league.id;
    });

    function handlePlayerClick(player) {
        if (selectedPlayers.includes(player)) {
            setSelectedPlayers(selectedPlayers.filter((p) => p.id !== player.id));
        } else {
            setSelectedPlayers([...selectedPlayers, player]);
        }
    }

    function handleMergeClick() {
        if (isEditMode) {
            const newSelectedPlayers = selectedPlayers.filter(
                (player) => player.id !== primaryPlayer.id
            );
            newSelectedPlayers.unshift(primaryPlayer);
            const id = getPlayersId(newSelectedPlayers);

            const newUser = { id, players: newSelectedPlayers };

            // replace the editing user with the new user in the final users array
            const newFinalUsers = finalUsers.map((user) => {
                if (user.id === editingUser.id) {
                    return newUser;
                }
                return user;
            });

            setFinalUsers(newFinalUsers);
            setEditingUser(null);
        } else {
            const newSelectedPlayers = selectedPlayers.filter(
                (player) => player.id !== primaryPlayer.id
            );
            newSelectedPlayers.unshift(primaryPlayer);
            const id = getPlayersId(newSelectedPlayers);

            setFinalUsers((prev) => {
                return [
                    ...prev,
                    {
                        id,
                        players: newSelectedPlayers,
                    },
                ];
            });

            setSelectedPlayers([]);
        }
    }

    const isLeagueMerged = !!finalUsers.find((user) => {
        return user.id === selectedPlayersId;
    });

    async function handleLeagueMerge() {
        const mergedUsers = finalUsers.map((user) => {
            const players = user.players;
            return {
                name: players[0].name,
                ids: players.map((u) => u.id),
            };
        });

        await updateLeague(leagueid, { users: mergedUsers });
        alert('Merged league succesfully!');
    }

    return (
        <BaseWrapper navigation>
            <div className="merge-leagues">
                <div className="merge-leagues__title-row">
                    <h1>
                        Merge Leagues
                        {isEditMode && (
                            <span className="merge-leagues__edit-mode">
                                {' '}
                                (Editing {editingUser.id})
                            </span>
                        )}
                    </h1>
                    <div className="merge-leagues__title-row-buttons">
                        {isEditMode && (
                            <Button title="Cancel Edit" onClick={() => setEditingUser(null)} />
                        )}
                        <Button title="Save Merge" onClick={handleLeagueMerge} />
                    </div>
                </div>

                <div className="merge-leagues__columns">
                    <LeagueColumn
                        selectedPlayers={selectedPlayers}
                        league={league1}
                        onClick={handlePlayerClick}
                    />
                    <LeagueColumn
                        selectedPlayers={selectedPlayers}
                        league={league2}
                        onClick={handlePlayerClick}
                    />

                    <div className="merge-leagues__column">
                        <h2>Players</h2>
                        {selectedPlayers.length > 0 && (
                            <>
                                <h3>Primary</h3>
                                <ul>
                                    {selectedPlayers.map((player) => {
                                        return (
                                            <li key={player.id}>
                                                <button
                                                    onClick={() => {
                                                        setPrimaryPlayer(player);
                                                    }}
                                                >
                                                    <div
                                                        className="player-primary"
                                                        data-primary={
                                                            primaryPlayer?.id === player.id
                                                        }
                                                    />
                                                    {player.name}
                                                </button>
                                            </li>
                                        );
                                    })}
                                </ul>

                                <Button
                                    title={
                                        isLeagueMerged && selectedPlayers.length >= 2
                                            ? 'Player combination already merged'
                                            : isEditMode
                                            ? 'Save Changes'
                                            : 'Merge'
                                    }
                                    onClick={handleMergeClick}
                                    disabled={selectedPlayers.length < 2 || isLeagueMerged}
                                    className="merge-leagues__merge-button"
                                />
                            </>
                        )}
                    </div>

                    <div className="merge-leagues__column">
                        <h2>Final Users</h2>
                        <ul>
                            {finalUsers.map((user) => {
                                return (
                                    <li key={user.id} className="merge-leagues__row-container">
                                        <button
                                            onClick={() => {
                                                setSelectedPlayers(user.players);
                                                setEditingUser(user);
                                            }}
                                            className="merge-leagues__final-user-btn"
                                        >
                                            {user.id}
                                        </button>
                                        <button
                                            aria-label="Delete"
                                            className="merge-leagues__final-user-delete-btn"
                                            onClick={(e) => {
                                                e.stopPropagation();
                                                setFinalUsers((prev) => {
                                                    return prev.filter((u) => u.id !== user.id);
                                                });
                                            }}
                                        >
                                            X
                                        </button>
                                    </li>
                                );
                            })}
                        </ul>
                    </div>
                </div>
            </div>
        </BaseWrapper>
    );
};

export default MergeLeagues;
