import React from 'react';
import { Link } from 'gatsby';
import LogoImage from '../../images/logo.jpeg';
import Image from '../theme/Image/index';
import { useUserStore } from '../../store/user';
import { NavigationWrapper } from './Navigation.style';

const Navigation = () => {
    const { user, logOut } = useUserStore((state) => {
        return {
            user: state.user,
            logOut: state.logOut,
        };
    });
    return (
        <NavigationWrapper>
            <div>
                <Link to="/">
                    <Image className="logo" src={LogoImage} alt="Fantasy Franchise" />
                </Link>

                <ul>
                    <li>
                        <Link to="/">Add League</Link>
                    </li>
                    <li>{user?.email}</li>
                    <li>
                        <button onClick={logOut}>Log Out</button>
                    </li>
                </ul>
            </div>
        </NavigationWrapper>
    );
};

export default Navigation;
