import { useUserStore } from '../store/user';
import { useDebounce } from './useDebounce';
import { useEffect, useState } from 'react';
import { navigate } from 'gatsby';

export function useProtectedRoute(options = {}) {
    const { userLogged = true, redirectTo = '/log-in' } = options;

    const [user, loading] = useUserStore((state) => [state.user, state.loading]);
    const [loaded, setLoaded] = useState(!loading);
    const debouncedLoaded = useDebounce(loaded, 1);
    // if userLogged is passed as an option we check to see if user is defined, else we check for
    // user not to? be defined
    const userCondition = userLogged ? !!user : !user;

    useEffect(() => {
        if (!loading && !userCondition) {
            navigate(redirectTo);
        } else if (!loading && userLogged && user && !user?.emailVerified) {
            navigate('/verify-message');
        } else {
            setLoaded(!loading);
        }
    }, [loading]);

    return { loaded: debouncedLoaded };
}
