import { getFirebase } from '../lib/firebase';
import { create } from 'zustand';
import { League, LeagueInput } from '../types/League';
import { User } from '../types/User';
import { generateDocumentLeagueId } from '../utils/leagueUtils';

const firebase = getFirebase();
const db = firebase.firestore();

export interface LeagueStore {
    leagues: League[] | null;
    loading: boolean;
    getLeagues: (user: User) => Promise<void>;
    removeLeague: (leagueId: string) => Promise<void>;
    updateLeague: (
        leagueId: string,
        data: Partial<League>,
        initialData?: LeagueInput
    ) => Promise<void>;
    createLeague: (data: LeagueInput) => Promise<void>;
    canLeagueBeClaimed: (leagueId: string, platform: string) => Promise<boolean>;
}

export const useLeagueStore = create<LeagueStore>((set, get) => ({
    leagues: null,
    loading: false,
    getLeagues: async (user) => {
        set({ loading: true });
        const { email, uid } = user;
        const [leaguesWithId, leaguesWithEmail] = await Promise.all([
            db.collection('users').where('userId', '==', uid).get(),
            db.collection('users').where('email', '==', email).get(),
        ]);

        const leagues: League[] = [];
        leaguesWithId.docs.forEach((league) => {
            leagues.push({
                id: league.id,
                ...league.data(),
            } as League);
        });

        leaguesWithEmail.docs.forEach((league) => {
            leagues.push({
                id: league.id,
                ...league.data(),
            } as League);
        });
        console.log({ leagues });

        set({
            leagues,
            loading: false,
        });
    },
    removeLeague: async (leagueId) => {
        await db.collection('users').doc(leagueId).delete();
        set({
            leagues: get().leagues?.filter((league) => league.id !== leagueId),
        });
    },
    updateLeague: async (leagueId, data, initialData?: LeagueInput) => {
        if ('platform' in data || 'leagueid' in data) {
            if (!initialData) {
                throw new Error('Initial data is required to update platform or leagueid');
            }

            const league = await db
                .collection('users')
                .where('leagueid', '==', data.leagueid || initialData.leagueid)
                .where('platform', '==', data.platform || initialData.platform)
                .get();

            if (league.docs.length > 0 && (league.docs[0].data() as League).userId !== '') {
                throw new Error(
                    `League ${data.platform || initialData.platform}-${
                        data.leagueid || initialData.leagueid
                    } already exists.`
                );
            }

            if ('slug' in data) {
                const leagueWithSlug = await db
                    .collection('users')
                    .where('slug', '==', data.slug)
                    .get();

                if (leagueWithSlug.docs.length > 0) {
                    throw new Error(`League with this url ${data.slug} already exists.`);
                }
            }
        }

        await db.collection('users').doc(leagueId).update(data);
        set({
            leagues: get().leagues?.map((league) => {
                if (league.id === leagueId) {
                    return { ...league, ...data };
                } else {
                    return league;
                }
            }),
        });
    },
    createLeague: async (leagueInput: LeagueInput) => {
        const slug = leagueInput.slug || generateDocumentLeagueId(leagueInput);
        delete leagueInput.email;
        delete leagueInput.name;

        leagueInput.userId = leagueInput.userId || firebase.auth().currentUser?.uid;
        if (!leagueInput.userId) {
            leagueInput.userId = '';
        }
        const testDoc = await firebase.firestore().collection('users').doc(slug).get();

        if (testDoc.exists === false) {
            try {
                await db
                    .collection('users')
                    .doc(slug)
                    .set({
                        ...leagueInput,
                        createdAt: firebase.firestore.Timestamp.fromDate(new Date()),
                    });
            } catch (err) {
                console.log(err);
                throw new Error('There was an error. Please check the data and try again.');
            }
        } else {
            throw new Error(`The league ${slug} already exists. Please try a different one.`);
        }
    },
    canLeagueBeClaimed: async (leagueId, platform): Promise<boolean> => {
        const league = await db
            .collection('users')
            .where('leagueid', '==', leagueId)
            .where('platform', '==', platform)
            .get();

        return league.docs.length > 0 && league.docs[0].data().userId === '';
    },
}));
